@import "../../assets/theme/clr_var.scss";

.modal_headerStyle {
  border-bottom: 1px solid $boderColor;
  margin-bottom: 25px;
}

.modal_headerStyle__rowA {
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  .modal_headerStyle__rowA_colLeft {
    display: flex;
    align-items: center;
    h2 {
      font-size: 18px;
      color: $white;
      font-weight: 400;
      margin-right: 5px;
    }
  }
  .modal_headerStyle__rowA_colRight {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}

.modal_headerStyle__rowB {
  padding-left: 20px;
  padding-right: 20px;
  .modal_headerStyle__rowB_searchInput {
    input {
      height: 66px;
      border-radius: 20px;
      border: 1px solid $boderColor;
      width: 100%;
      padding-left: 20px;
      padding-right: 20px;
      box-sizing: border-box;
      background: transparent;
      font-family: $baseFont;
      font-size: 16px;
      font-weight: 500;
      color: $white;
      @media (max-width: 767px) {
        height: 50px;
      }
      &:focus-visible,
      &:focus {
        outline: none;
        box-shadow: none;
      }
    }
  }
}

.modal_headerStyle__rowC {
  padding-left: 20px;
  padding-right: 20px;
  .modal_headerStyle__rowC_colLeft {
    display: flex;
    align-items: center;
    h2 {
      font-size: 16px;
      color: $white;
      font-weight: 400;
      margin-right: 5px;
    }
  }
  .modal_headerStyle__rowC_colRight {
    display: flex;
    justify-content: flex-end;
    button {
      padding: 0;
      background: transparent;
      border: 0px;
      cursor: pointer;
      color: #fff;
      font-weight: bold;
    }
  }
}

.tokenList__column {
  height: 250px;
  overflow-y: auto;
  overflow-x: hidden;

  .tokenList {
    box-sizing: border-box;
    li {
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .dis {
        opacity: 0.5;
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
      }
      .tokenName_textStyle {
        font-size: 16px;
        color: $white;
        font-weight: 400;
        margin-left: 10px;
      }
    }
  }
}
.confirm_list {
  padding: 20px;
  li {
    margin: 0 0 8px 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    img {
      width: 20px;
    }
  }
}
.Confirm_btn {
  margin-top: 0px;
  padding-top: 10px;
}
.swap_confirmation li {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.3rem;
  align-items: center;
}
