@import "../../assets/theme/clr_var.scss";

.connect_wallet {
  .modal-dialog {
    max-width: 335px;
    margin: 0 auto;
    @media (max-width: 374px) {
      max-width: 310px;
    }
    .modal-body {
      display: flex;
      padding: 27px;
      flex-direction: column;
    }
    .modal-content {
      background: $baseColor-4;
      color: $white;
      border: 1px solid $boderColor;
      border-radius: 8px;
      overflow: unset;
      .modal-header {
        padding: 24px;
        position: relative;
        justify-content: center;
        border-bottom: 1px solid $boderColor;

        .modal-title {
          font-size: 11px;
          font-weight: 700;
          line-height: 11px;
        }
        .btn-close {
          background: url(../../assets/images/token_icons/saitamaIcons/crossIcon.svg)
            center no-repeat;
          outline: none;
          background-size: 12px;

          opacity: 1;
          border-radius: 50px;
          position: absolute;
          right: -40px;
          top: 7px;
          width: 20px;
          height: 20px;
          padding: 5px;
          border: 1px solid $boderColor;
          background-color: $baseColor-2;
          &:focus {
            box-shadow: none;
          }
          @media (max-width: 767px) {
            top: -50px;
            right: 50%;
            transform: translate(50%, 0);
          }
        }
      }
      .baseToken_style.token_strut {
        padding-top: 20px;
        @media (max-width: 767px) {
          padding-top: 0;
        }
        ul {
          padding-left: 0;
          li {
            display: block;
            margin-right: 0;
            margin-bottom: 13px;
            button.btn {
              padding: 10px 17px;
              border-radius: 19px;
              width: 100%;
              text-decoration: none;
              font-size: 11px;
              font-weight: 500;
              color: $white;
              font-family: "Montserrat", sans-serif;
              display: block;
              text-align: left;
              background: $dimbg-3;
              position: relative;
              border: 0;
              height: auto;
              transition: all 0.2s;
              @media (max-width: 767px) {
                padding: 12px 20px;
                font-size: 13px;
              }
              &:hover {
                opacity: 0.8;
              }
              &:focus {
                box-shadow: none;
              }
              img {
                width: 25px;
                height: 25px;
                position: absolute;
                right: 7px;
                top: 6px;
                background: $white;
                border-radius: 50%;
              }
            }
          }
        }
      }
    }
  }
}
