@import "../../assets/theme/clr_var.scss";

.linkBtn {
  cursor: pointer;
  text-decoration: none;
  padding: 0 20px;
  background: $secondaryColor;
  line-height: 32px;
  border-radius: 25px;
  height: 32px;
  font-size: 11px;
  font-weight: 700;
  color: $white;
  border: 0;
  font-family: $baseFont;
  z-index: 1;
  transition: all ease 0.5s;
  position: relative;
  overflow: hidden;
  display: inline-block;
  line-height: 34px;

  &:focus {
    box-shadow: unset;
    outline: unset;
  }
  img {
    display: none;
  }
  &:after {
    position: absolute;
    content: "";
    background-image: linear-gradient(268deg, #8b26f4 0%, #c996cc 100%);
    width: 100%;
    height: 100%;
    z-index: -1;
    border-radius: 25px;
    top: 0;
    opacity: 0;
    left: 100%;
    transition: all ease 0.5s;
  }
  &:hover {
    color: #fff;
    &:after {
      opacity: 1;
      left: 0;
      transition: all ease 0.5s;
    }
  }
}
