@import "../../assets/theme/clr_var.scss";

.buttonStyle.btn {
  cursor: pointer;
  padding: 5px 10px;
  background-image: $gradientColor-2;
  border-radius: 25px;
  min-width: 93px;
  height: 32px;
  font-size: 11px;
  font-weight: 700;
  color: $white;
  border: 0;
  font-family: $baseFont;
  z-index: 1;
  transition: all ease 0.5s;
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 29px;

  &:focus {
    box-shadow: unset;
    outline: unset;
  }
  img {
    display: none;
  }
  &:after {
    position: absolute;
    content: "";
    background: transparent linear-gradient(268deg, #c996cc 0%, #8b26f4 100%)
      padding-box;
    width: 100%;
    height: 100%;
    z-index: -1;
    border-radius: 25px;
    top: 0;
    opacity: 0;
    left: 100%;
    transition: all ease 0.5s;
  }
  &:hover {
    color: #fff;
    &:after {
      opacity: 1;
      left: 0;
      transition: all ease 0.5s;
    }
  }
}
.logout_btn {
  img {
    margin-bottom: 0 !important;
  }
