@import "../../assets/theme/clr_var.scss";

.profileModal_sec {
  p {
    font-size: 11px;
    font-weight: 700;
    margin-bottom: 15px;
  }

  .copySec {
    display: flex;
    margin-bottom: 40px;
    justify-content: center;
    align-items: center;
    a,
    span {
      text-decoration: none;
      color: $secondaryColor;
      font-weight: 500;
      font-size: 11px;
    }
    a {
      transition: all 0.2s;
      &:hover {
        color: $white;
      }
    }

    img {
      width: 15px;
      margin-left: 15px !important;
      display: inline-block !important;
      margin-bottom: 0 !important;
    }
  }
}
