.scroll_top {
  cursor: pointer;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
.d-center {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.information-box {
  width: 560px;
  margin: 0 auto;
  padding: 40px;
  border-radius: 10px;
  border: 1px solid $secondaryColor;
  background: $baseColor;
  text-align: center;
}
.py-40 {
  padding-top: 40px;
  padding-bottom: 40px;
  @media (max-width: 767px) {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
.py-60 {
  padding-top: 60px;
  padding-bottom: 60px;
  @media (max-width: 767px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}
.py-80 {
  padding-top: 80px;
  padding-bottom: 80px;
  @media (max-width: 767px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.outerWrap {
  display: flex;
  width: 100%;
  padding: 37px 45px;

  @media (max-width: 991px) {
    min-height: calc(100vh - 130px);
  }
  @media (max-width: 767px) {
    padding: 20px;
  }
  .container_wrap {
    padding-left: 210px;
    padding-bottom: 75px;
    width: 100%;

    @media (max-width: 991px) {
      padding-left: 0;
      padding-bottom: 0;
    }
  }
}
